import cdnConfig from '~global/variables/cdn-config.json';
import getEnvironment, { environments } from '~global/js/helpers/environment';

const imgCdnPaths = {
  [environments.DEV]: cdnConfig.imgCdnUrls.dev,
  [environments.PROD]: cdnConfig.imgCdnUrls.prod,
  [environments.STAGE]: cdnConfig.imgCdnUrls.stage,
  [environments.UAT]: cdnConfig.imgCdnUrls.uat,
};

export default function getImgCdnBasePath() {
  return imgCdnPaths[getEnvironment()];
}



