import getImgCdnBasePath from '~global/js/helpers/imgCdn';
import imgSizes from '~global/variables/imgSizes.json';

export default function getImageUrl(imagePath, filterName) {
  return `${getImgCdnBasePath()}/${imgSizes[filterName]}/${imagePath}`;
}



